.NoteList .NoteList__items {
  list-style: none;
  display: -ms-inline-flexbox;
  display: inline-flex;
  width: 100%;
  max-width: 400px;
  background: white;
  -ms-flex-pack: center;
      justify-content: center;
  border: 1px black solid;
  height: 300px;
  margin: 5px;
  -webkit-box-shadow: 2px 2px black;
          box-shadow: 2px 2px black;
  border-radius: 3%;
  overflow: hidden; 
  white-space: nowrap;
  overflow-y: hidden;
}

.NoteList .NoteList__items h2 {
  width: 100%;
  font-weight: bold;
  text-decoration: underline;
}

.NoteList__body {
  padding: 3px;
}

.NoteList .NoteList__items p {
  font-size: 1.1rem;
  white-space: initial;
}

.NoteEdits .NoteEdits__components {
  display: -ms-inline-flexbox;
  display: inline-flex;
  width: 100%
}


